//#region New Login
.login-page-wrapper {
  background-image: url("./assets/images/planet-gradient-background.webp");
  background-size: cover;
  background-position: top left;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  .login-header {
    display: flex;
    align-items: center;
    max-width: 1100px;
    padding: 15px 30px;
    margin: auto;
    ul.menu-items {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      padding: 0;
      margin: 0;
      list-style: none;
      flex: 1;
      margin-left: 35px;
      li {
        a {
          color: white;
          margin-right: 30px;
          font-weight: normal;
          line-height: 26px;
          cursor: pointer;
          transition: all .1s ease-in-out;
          will-change: color;
          &:hover {
            color: $hyper-link;
            border-bottom: 2px solid $hyper-link;
          }
        }
        @media (max-width: 767px) {
          display: none;
        }
      }
    }
  }
  .inner-login-wrapper {
    display: flex;
    .market-image-wrapper {
      flex: 1 1 50%;
      img {
        max-width: 100%;
        margin-top: 120px;
      }
      @media (max-width: 767px) {
        display: none;
      }
    }
    .right-content-wrapper {
      flex: 1 1 50%;
      padding: 0 35px 35px 35px;
      max-width: 700px;
      @media (min-width: 768px) {
        flex: 1 1 50%;
        padding: 0 35px 35px 35px;
        max-width: 700px;
      }
      .quest-counter-wrapper {
        background-color: #2E2959;
        padding: 30px 30px 60px 30px;
        position: relative;
        margin-top: 55px;
        border-radius: 10px;
        .header-lg {
          color: $body-text-yellow;
          text-align: center;
          margin-bottom: 20px;
        }
        .quest-counter-paragraph-lg {
          font-weight: bold;
          font-size: 37px;
          line-height: 150%;
          color: $white-color;
          margin: 0;
         > span {
            .xl-lg {
              font-size: 57px;
            }
            &:first-child {
              margin-left: 0;
            }
           @media (min-width: 767px) {
             margin: 0 0 0 30px;
           }
           @media (max-width: 767px) {
             display: flex;
           }
          }
        }
        .avatar-bag-holder {
          position: absolute;
          right: -60px;
        }
      }
    }
  }
  @media (max-width: 767px) {
    padding: 25px 25px 180px 25px;
    .inner-login-wrapper {
      .right-content-wrapper {
        padding: 0;
        button {
          width: 100%;
        }
        .quest-counter-wrapper {
          margin-top: 35px;
          .avatar-bag-holder {
            right: 0;
          }
        }
      }
    }
    .login-header {
      padding: 0 0 20px 0;
    }
  }
  @media (min-width: 768px) {
    padding: 0 40px 160px 0;
  }
}
.choose-avatar{
  display: flex;
  margin-top: 15px;
  @media (max-width: 767px) {
    overflow: auto;
    padding-bottom: 10px;
  }
  .avatar {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 25px;
    width: 135px;
    height: 135px;
    min-width: 135px;
    border-radius: 10px;
    background-color: rgba(63, 35, 85, .5);
    box-shadow: 0 3px 0 0 rgba(25, 17, 66, 0.86);
    transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
    cursor: pointer;
    transform: translateZ(0);
    &:after {
      content: '';
      position: absolute;
      display: block;
      border-radius: 50%;
      background-color: red;
      background-image: linear-gradient(
          145deg
        , #024304 0%, #03fe79 100%);
      background-image: -ms-linear-gradient(145deg, #024304 0%, #03fe79 100%);
      width: 29px;
      height: 29px;
      top: auto;
      bottom: -10px;
      right: -10px;
      left: auto;
      box-shadow: 2px 4px 1px 0 rgb(25 17 66 / 86%);
      transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
      transform: scale(0);
    }
    &:before {
      content: "";
      position: absolute;
      display: block;
      background-image: url("assets/images/icons/icon-tick.png");
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
      width: 17px;
      height: 17px;
      top: auto;
      bottom: -4px;
      right: -4px;
      left: auto;
      z-index: 999;
      transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
      transform: scale(0);
    }
    &.active {
      position: relative;
      background-color: rgba(63, 35, 85, 1);
      &:after {
        transform: scale(1)
      }
      &:before {
        transform: scale(1)
      }
    }
    &:hover {
      transform: scale(1.1);
      filter: brightness(1.1);
      background-color: rgba(63, 35, 85, 1);

    }
    &:last-child {
      margin-right: 0;
    }
    img {
      max-width: 100%;
    }
  }
}
//#endregion New Login

//#region Account page
  .account-page-wrapper {
    .account-row {
      .column-panel {
        background: linear-gradient(
            0deg
          , rgba(123, 80, 155, 0.9) 0%, rgba(103, 67, 131, 0.9) 22.44%, rgba(90, 58, 114, 0.95) 100%);
        border-radius: 10px;
        position: relative;
        z-index: 1;
        height: 265px;
        &:after {
          content: "";
          background: rgb(90 58 114 / 50%);
          box-shadow: 2px 4px 2px rgb(25 17 66 / 51%);
          border-radius: 10px;
          position: absolute;
          left: 0;
          right: 0;
          bottom: 0;
          top: 0;
        }
      }
      .column-panel-dark {
        border-radius: 10px;
        background-color: #2E2959;
        height: 265px;
        position: relative;
        transform: translateZ(0);
        overflow: hidden;
        .confetti > .before, .confetti > .after {
          left: -10px;
          top: -60px;
        }
      }
      .bottom-right-column {
        margin-top: 25px;
      }
      .bottom-left-column {
        margin-top: 25px;
        .avatar-info {
          padding: 15px;
          width: 100%;
          .info-item {
            z-index: 1024;
            position: relative;
          }
        }
        .unlock-avatar-quest {
          position: relative;
          z-index: 1024;
          padding: 15px;
          width: 100%;
          h1 {
            font-size: 26px;
            color: white;
            text-align: center;
          }
          span {
            font-style: normal;
            font-weight: bold;
            font-size: 26px;
            line-height: 150%;
            display: block;
            color: #FFDE00;
            text-align: center;
            margin-bottom: 20px;
          }
          button {
            width: 100%;
            margin: auto;
            white-space: nowrap;
            &:hover {
              transform: scale(1.025);
            }
          }
        }
      }
      &.upper-box {
        border-bottom: 1px solid rgba(255,255,255, 0.3);
        padding-bottom: 35px;
      }
      @media (min-width: 1200px) {
        .top-left-column {
          padding-left: 0;
        }
        .top-right-column {
          padding-right: 0;
        }
        .middle-bottom-column {
          margin-top: 25px;
        }
        .bottom-left-column {
          margin-top: 25px;
        }
        .bottom-right-column {
          padding-right: 0;
        }
      }
      @media (max-width: 1199.98px) {
        .top-right-column {
          margin-top: 25px;
          padding-right: 0;
          > h1 {
            margin-top: 25px;
          }
        }
      }
    }
    .avatar-profile-information {
      display: flex;
      .left-column-avatar {
        padding: 15px;
        border-right: 1px solid rgba(255, 255, 255, 0.5);
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
        min-width: 350px;
        flex: 50%;
        margin-right: 10px;
        position: relative;
        .avatar-bag,
        .avatar-pet {
          img {
            height: 145px;
          }

        }
        .avatar-bag,
        .avatar,
        .avatar-pet {
          border: 1px solid;
          position: relative;
          z-index: 1024;
          border-radius: 5px;
          height: 100%;
          display: flex;
          align-items: flex-end;
          img {
            margin-bottom: 20px;
          }
          &:before {
            content: '';
            display: block;
            position: absolute;
            left: 50%;
            transform: translateX(-50%) scale(0);
            bottom: -50px;
            background-color: $dark-blue;
            color: white;
            padding: 3px 22px;
            border-radius: 5px;
            z-index: 99999;
            font-size: 16px;
            transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
          }
          &:after {
            content: '';
            background-position: center;
            background-repeat: no-repeat;
            background-size: contain;
            width: 40px;
            height: 40px;
            display: block;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            bottom: -20px;
          }
          &.rare {
            border-color: #ff7f00;
            &:after {
              background-image: url("~/assets/images/avatars/rarity-stars/rare-star-badge.svg");
            }
            &:before {
              content: 'Rare';
            }
          }
          &.epic {
            border-color: #FFE600;
            &:after {
              background-image: url("~/assets/images/avatars/rarity-stars/epic-star-badge.svg");
            }
            &:before {
              content: 'Epic';
            }
          }
          &.legendary {
            border-color: #D4D3D7;
            &:after {
              background-image: url("~/assets/images/avatars/rarity-stars/legendary-star-badge.svg");
            }
            &:before {
              content: 'Legendary';
            }
          }
          &.mythic {
            border-color: #00E413;
            &:after {
              background-image: url("~/assets/images/avatars/rarity-stars/mythic-star-badge.svg");
            }
            &:before {
              content: 'Mythic';
            }
          }
          &.exotic {
            border-color: #00FFFF;
            &:after {
              content:'';
              background-image: url("~/assets/images/avatars/rarity-stars/exotic-star-badge.svg");
            }
            &:before {
              content: 'Exotic';
            }
          }
          &:hover:before {
            transform: translateX(-50%) scale(1);
          }
        }
        h3 {
          width: 100%;
          display: flex;
          justify-content: center;
          z-index: 1024;
        }
      }
      .right-column-avatar {
        margin-left: 10px;
        flex: 50%;
      }
    }
    .nft-alert-wrapper {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 25px;
      .nft-alert-box {
        z-index: 1024;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        > img {
          margin-bottom: 20px;
        }
        .arrow-indicator {
          position: absolute;
          right: -40px;
        }
      }
    }
    .skillz-info {
      padding: 25px;
      .skillz-content {
        z-index: 1024;
        position: relative;
        > h3 {
          margin-bottom: 15px;
        }
        .info-item {
          h3 {
            min-width: 95px;
          }
        }
      }
    }
    .banner {
      padding: 25px;
      display: flex;
      justify-content: center;
      align-items: center;
      .banner-content {
        .header-lg {
          color: $body-text-yellow;
          text-align: center;
          margin-bottom: 20px;
          font-size: 40px;
        }
        .banner-paragraph-lg {
          font-family: Myriad Pro;
          font-style: normal;
          font-weight: bold;
          font-size: 27px;
          line-height: 150%;
          color: $white-color;
          .xl-lg {
            font-size: 44px;
          }
        }
      }
    }
    .portfolio-value  {
      .charts {
        display: flex;
        .chart-column {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          .char-value {
            position: absolute;
            top: calc(50% - 10px);
            transform: translateY(-50%);
            opacity: .5;
          }
          img {
            max-width: 100%;
            margin: 0 10px 10px 10px;
          }
          .chart-description {
            display: flex;
            flex-direction: column;
            align-items: center;
            .chart-value-description {
              opacity: .8;
            }
          }
        }
      }
    }
    .quests {
      padding: 12px 10px 0 10px;
      .view-all-quests {
        display: flex;
        align-content: center;
        justify-content: center;
        position: relative;
        z-index: 1024;
      }
    }
  }
//#endregion

//#region Mining-pool

.mining-pool-page-wrapper {
  .gold-mines-blurbs {
    background-color: $dark-blue;
    padding: 15px;
    border-radius: 5px;
    display: flex;
    .option-blurb {
      background-color: $light-blue;
      border-radius: 5px;
      margin-right: 20px;
      flex: 1 1 25%;
      &:last-child {
        margin-right: 0;
      }
      .option-blurb-header {
        background: rgba(255, 255, 255, 0.06);
        border-radius: 5px 5px 0 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 4px 15px;
        h4 {
          font-family: Myriad Pro;
          font-style: normal;
          font-weight: normal;
          font-size: 18px;
          line-height: 150%;
          color: #FFFFFF;
          text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
          margin-bottom: 0;
        }
        img {
          height: 15px;
        }
      }
      .option-blurb-body {
        padding: 10px 15px;
        ul {
          padding: 0;
          margin: 0;
          list-style: none;
          li {
            display: flex;
            justify-content: space-between;
            align-items: center;
            span {
              font-size: 14px;
              color: $white-color;
              &.highlighted {
                color: $body-text-yellow;
              }
              &.value:not(.highlighted) {
                opacity: .7;
              }
            }
          }
        }
      }
      .option-blurb-footer {
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-top: 1px solid rgba(255, 255, 255, 0.1);
        padding: 10px 15px;
        button {
          margin: 0;
        }
      }
    }
  }
  .stats {
    .stats-wrapper {
      background-color: $dark-blue;
      height: 270px;
      padding: 15px;
      border-radius: 5px;
      display: flex;
      .mountain-chart {
        flex: 1;
        background-color: $light-blue;
        margin-right: 15px;
        padding: 15px;
        border-radius: 5px;
        .chart-tools {
          display: flex;
          justify-content: space-between;
          align-items: center;
          .chart-name {
            font-size: 16px;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            img {
              height: 20px;
              position: relative;
              top: -2px;
            }
          }
          .filters {
            display: flex;
            justify-content: space-between;
            width: 100%;
            .time-filters {
              padding: 0;
              margin: 0 0 0 40px;
              list-style: none;
              display: flex;
              li {
                margin-right: 10px;
                cursor: pointer;
                &.active {
                  span {
                    border-bottom: 3px solid $blue-status;
                    opacity: 1;
                  }
                }
                span {
                  font-size: 14px;
                  line-height: 150%;
                  color: #FFFFFF;
                  opacity: 0.5;
                }
              }
            }
            .price-analysis {
              display: flex;
              justify-content: center;
              align-items: center;
              .analysis-item {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: flex-end;
                margin-right: 15px;
                &:last-child {
                  margin-right: 0;
                }
                .value {
                  font-weight: bold;
                  font-size: 14px;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                }
                .name {
                  font-weight: normal;
                  font-size: 8px;
                  line-height: 10px;
                  color: #FFFFFF;
                  opacity: 0.5;
                }
                &.low {
                  .value {
                    color: $error;
                    &:after {
                      content: '';
                      width: 0;
                      height: 0;
                      border-style: solid;
                      border-width: 5px 5px 0 5px;
                      border-color: $error transparent transparent transparent;
                      margin-left: 5px;
                    }
                  }
                }
                &.high {
                  .value {
                    color: $dark-green;
                    &:after {
                      content: '';
                      width: 0;
                      height: 0;
                      border-style: solid;
                      border-width: 0 5px 5px 5px;
                      border-color: transparent transparent $dark-green transparent;
                      margin-left: 5px;
                    }
                  }
                }
              }
            }
          }
        }
        .chart-stream {
          img {
            width: 100%;
          }
        }
      }
      .pie-chart {
        background-color: $light-blue;
        width: 250px;
        padding: 15px;
        border-radius: 5px;
        position: relative;
        ul {
          padding: 0;
          margin: 0;
          list-style: none;
          li {
            display: flex;
            justify-content: space-between;
            align-items: center;
            span {
              font-size: 14px;
              &.value {
                opacity: .7;
              }
            }
          }
        }
        .chart-image {
          position: absolute;
          left: 0;
          right: 0;
          top: 10px;
          display: flex;
          justify-content: center;
          align-items: center;
          img {
            max-width: 100%;
          }
        }
        .chart-stats {
          display: flex;
          flex-direction: column;
          align-items: center;
          z-index: 9999;
          position: absolute;
          top: calc(50% + 5px);
          left: 50%;
          transform: translate(-50%, -50%);
          .name {
            color: #FFFFFF;
            opacity: 0.4;
          }
          img {
            height: 20px;
          }
        }
      }
    }
  }
}

//#endregion Mining-pool

