//#region app-header
.app-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: transparent;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  padding: 15px 30px;
  background-image: url("./assets/images/account-background.webp");
  background-size: cover;
  .menu-items {
    padding: 0;
    margin: 0 0 0 35px;
    display: flex;
    list-style: none;
    li {
      a {
        display: flex;
        font-weight: bold;
        font-size: 20px;
        line-height: 26px;
        color: #FFFFFF;
        margin-right: 30px;
        cursor: pointer;
        transition: all 0.1s ease-in-out;
        border-bottom: 2px solid transparent;
        &:hover {
          text-decoration: none;
          color: $hyper-link;
          border-bottom: 2px solid $hyper-link;
        }
        &.is-active {
          color: $hyper-link;
          &:hover {
            text-decoration: none;
            cursor: default;
          }
        }
      }
    }
  }
  .logo {
    height: 50px;
    cursor: pointer;
  }
  .header-lg {
    color: #FFFFFF;
  }
  .wallet-button {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-right: 20px;
    .arrow-indicator {
      margin: 0 20px;
      img {
        height: 35px;
        transform: scaleX(-1) rotate(55deg);
      }
    }
    .link {
      font-size: 18px;
      line-height: 1;
    }
  }
  .tool-bar {
    display: flex;
    .notification-center {
      position: relative;
      padding: 15px 0;
      .badge {
        position: absolute;
        right: -10px;
        top: 2px;
        background-color: #E20000;
        border-radius: 23px;
        min-width: 23px;
        cursor: pointer;
      }
      .notifications {
        position: absolute;
        background-color: $pink-color;
        box-sizing: border-box;
        box-shadow: 0 4px 4px rgb(0 0 0 / 25%), 0px 4px 4px rgb(0 0 0 / 25%);
        border-radius: 15px;
        min-width: 270px;
        top: 71px;
        right: -20px;
        padding: 10px 0 0 0;
        transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
        transform: scale(0);
        .show-more {
          padding: 0 20px;
          width: 100%;
          display: flex;
          justify-content: center;
          margin: 10px 0;
        }
        .messages,
        .new-quests {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 10px 20px 10px 20px;
          border-top: 1px solid rgba(255, 255, 255, 0.3);
          cursor: pointer;
          transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
          &:hover {
            filter: brightness(1.1);
            background: rgba(255, 255, 255, 0.15);
          }
        }
        ul {
          padding: 0;
          margin: 0;
          list-style: none;
          > h3 {
            padding: 0 20px;
          }
          li {
            padding: 10px 20px;
            font-size: 18px;
            cursor: pointer;
            transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
            &:nth-child(odd) {
              background: rgba(255, 255, 255, 0.15);
            }
            &:hover {
              filter: brightness(1.1);
              background: rgba(255, 255, 255, 0.15);
            }
          }
        }
        &:after {
          content: '';
          background-image: url("./assets/images/icons/icon-menu-chevron.svg");
          width: 45px;
          height: 11px;
          display: block;
          position: absolute;
          top: -11px;
          right: 20px;
        }
      }
      &.open {
        .notifications {
          transform: scale(1);
        }
      }
    }
    .settings-menu {
      position: relative;
      padding: 15px 0;
      .menu-list {
        padding: 0;
        margin: 0;
        position: absolute;
        list-style: none;
        display: flex;
        flex-direction: column;
        white-space: nowrap;
        background: #D34493;
        box-sizing: border-box;
        box-shadow: 0 4px 4px rgb(0 0 0 / 25%), 0px 4px 4px rgb(0 0 0 / 25%);
        border-radius: 10px;
        right: -30px;
        top: 70px;
        transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
        transform: scale(0);
        &:after {
          content: '';
          background-image: url("./assets/images/icons/icon-menu-chevron.svg");
          width: 45px;
          height: 11px;
          display: block;
          position: absolute;
          top: -11px;
          right: 30px;
        }
        .list-item {
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding: 7px 15px;
          border-bottom: 1px solid rgb(255 255 255 / 30%);
          font-family: DK Lemon Yellow Sun;
          font-size: 18px;
          line-height: 150%;
          font-style: normal;
          font-weight: normal;
          font-size: 18px;
          line-height: 150%;
          letter-spacing: 2px;
          color: $body-text-yellow;
          cursor: pointer;
          transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
          background: #D34493;
          position: relative;
          .languages {
            position: absolute;
            bottom: auto;
            top: 50px;
            left: 0;
            right: 0;
            background: $pink-color;
            transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
            box-shadow: 0 4px 4px rgb(0 0 0 / 25%), 0px 4px 4px rgb(0 0 0 / 25%);
            transform:scale(0);
            border-radius: 0 0 10px 10px;
            max-height: 200px;
            overflow: auto;
            ul {
              padding: 0;
              margin: 0;
              list-style: none;
              li {
                padding: 10px 15px;
                cursor: pointer;
                transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
                border-bottom: 1px solid rgb(255 255 255 / 30%);
                display: flex;
                justify-content: space-between;
                align-items: center;
                .custom-checkbox {
                  display: block;
                  position: relative;
                  right: -4px;
                  .custom-control-label::before {
                    min-width: 28px;
                    min-height: 27px;
                  }
                }
                &:last-of-type {
                  border-bottom: none;
                }
                &:hover {
                  background-color: #cc257f;
                }
              }
            }
          }
          &.open {
            z-index: 99999;
            .languages {
              transform: scale(1);
            }
          }
          &:hover {
            filter: brightness(1.1);
          }
          &:first-child {
            border-top-left-radius: 10px;
            border-top-right-radius: 10px;
          }
          &:last-child {
            border-bottom: 0;
            border-bottom-left-radius: 10px;
            border-bottom-right-radius: 10px;
          }
          .slider-wrapper {
            .slider-background {
              width: 182px;
              margin-left: 15px;
            }
          }
          a {
            width: 100%;
            font-family: DK Lemon Yellow Sun;
            color: $body-text-yellow;
            font-style: normal;
            font-weight: normal;
            font-size: 18px;
            line-height: 150%;
            letter-spacing: 2px;
            &:hover {
              text-decoration: none;
            }
          }
        }
      }
      &:hover {
        .menu-list {
          transform: scale(1);
        }
      }
    }
    img {
      cursor: pointer;
    }
    .app-header-icon {
      height: 35px;
    }
  }
}
//#endregion app-header

//#region progress bar
.bag-progress-bar {
  flex: 1;
  background: linear-gradient(180deg, #3A265B 0%, #5C3C75 100%);
  border-radius: 5px;
  margin-left: 10px;
  position: relative;
  .bag-progress-fill {
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 1;
    padding: 4px;
    background-image: url('~/assets/images/primary-gradient.svg');
    background-size: cover;
    background-repeat: no-repeat;
    width: 60%;
    border-radius: 5px;
    border: 2px solid $dark-green;
    height: 25px;
    transform: translateZ(0);
    .bag-fill-percentage {
      font-style: normal;
      font-weight: bold;
      font-size: 14px;
      text-align: right;
      letter-spacing: 1px;
      color: #FFFFFF;
      position: absolute;
      z-index: 99999;
      left: 50%;
      top: calc(50% + 2px);
      transform: translate(-50%, -50%);
    }
  }
  .bag-progress-bar-values {
    display: flex;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    .bag-value-distance {
      height: 25px;
      width: calc(100% / 5);
      position: relative;
      &:after {
        content: "";
        display: block;
        background: linear-gradient(180deg, #311B56 0%, #B694D0 100%);
        border-radius: 4px;
        height: 19px;
        opacity: .5;
        width: 1px;
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%) rotate(
            180deg
        );
      }
      &.active {
        &:after {
          background: linear-gradient(
              180deg
            , #52FF00 0%, #319900 59.57%, #2A8301 100%);
        }
      }
      &:last-child {
        &:after {
          display: none;
        }
      }
    }
  }
}
//#endregion progress bar

//#region Tabs
.bagTabs {
  display: flex;
  flex-wrap: wrap;
  z-index: 1024;
  width: 100%;
  input[type="radio"] {
    display: none;
  }
  input[type='radio']:checked + label + .tab {
    display: block;
  }
  input[type="radio"]:checked + label {
    background: linear-gradient(180deg, #7B509B 0%, #674383 23.44%, #5A3A72 100%);
    border-radius: 10px 10px 0 0;
    opacity: 1;
    span {
      opacity: 1;
    }
  }
  label {
    font-family: DK Lemon Yellow Sun;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 150%;
    background: #493658;
    border-radius: 10px 10px 0 0;
    flex: 1 1 auto;
    height: 45px;
    padding: 10px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 15px;
    cursor: pointer;
    letter-spacing: 2px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0;
    span {
      opacity: .7;
      transition: opacity .3s linear;
    }
    &:last-of-type{
      margin-right: 0;
      box-shadow: 2px 4px 2px rgba(25, 17, 66, 0.51);
      border-radius: 10px 10px 0 0;
    }
    &:hover {
      span {
        opacity: 1;
      }
    }
  }
  .tab {
    width: 100%;
    background: transparent;
    order: 1;
    display: none;
  }
  .bag-tabs-content {
    background: linear-gradient(
        0deg
      , rgba(123, 80, 155, 0.9) 0%, rgba(103, 67, 131, 0.9) 22.44%, rgba(90, 58, 114, 0.95) 100%);
    border-radius: 0 0 10px 10px;
    position: relative;
    height: 220px;
    &:after {
      content: "";
      background: rgba(90, 58, 114, 0.5);
      box-shadow: 2px 4px 2px rgb(25 17 66 / 51%);
      border-radius: 0 0 10px 10px;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
    }
    .tabs-inner-content {
      position: relative;
      z-index: 1024;
      height: 100%;
      .coins {
        margin: auto;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        padding: 0 25px;
        .coin {
          display: flex;
          flex-direction: column;
          align-items: center;
          margin-right: 20px;
          cursor: pointer;
          transform: translateZ(0);
          position: relative;
          min-width: 95px;
          &:hover {
            .bag-tooltip {
              transform: translateX(-50%) scale(1) !important;
            }
          }
          &:last-child {
            margin-right: 0;
          }
          .coin-name {
            font-family: Myriad Pro;
            font-style: normal;
            font-weight: bold;
            font-size: 17px;
            line-height: 150%;
            text-align: center;
            letter-spacing: 1px;
            color: #FFFFFF;
          }
          .coin-image {
            margin-top: 10px;
          }
        }
      }
    }
  }
}
//#endregion Tabs

//#region Tooltip
.bag-tooltip {
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 15px;
  position: absolute;
  white-space: nowrap;
  top: 110px;
  left: 50%;
  transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
  transform: translateX(-50%) scale(0);
  z-index: 99999;
  background-color: #D34493;
  .tooltip-body {
    ul {
      padding: 5px 0;
      margin: 0;
      list-style: none;
      li {
        padding: 5px 15px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .item-name {
          font-family: DK Lemon Yellow Sun;
          font-style: normal;
          font-weight: normal;
          font-size: 18px;
          line-height: 150%;
          letter-spacing: 2px;
          color: #FFDE00;
          text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
          margin-right: 25px;
        }
        .item-value {
          font-size: 18px;
          line-height: 150%;
          text-align: right;
          color: #FFFFFF;
        }
        &:first-child {
          border-top-left-radius: 15px;
          border-top-right-radius: 15px;
        }
        &:hover {
          span {
            filter: brightness(1.1);
          }
        }
      }
    }
  }
  .tooltip-footer {
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 1px solid rgba(255,255,255, .3);
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
    transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
    span {
      font-family: DK Lemon Yellow Sun;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 150%;
      letter-spacing: 2px;
      color: #FFDE00;
      text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    }
    &:hover {
      filter: brightness(1.1);
    }
  }
  &:after {
    content: '';
    background-image: url("./assets/images/icons/icon-menu-chevron.svg");
    width: 45px;
    height: 11px;
    display: block;
    position: absolute;
    top: -9px;
    left: calc(50% - 3px);
    transform: translateX(-50%);
  }
}
//#endregion Tooltip

//#region Accordion
.bag-accordions-panel {
  height: 217px;
  overflow: hidden;
  padding: 0 5px;
  .bag-accordion {
    position: relative;
    z-index: 1024;
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
    padding: 9px 5px;
    cursor: pointer;
    &:hover {
      background: rgb(255 255 255 / 10%);
    }
    .accordion-header {
      display: flex;
      align-items: center;
      justify-content: center;
      .quest-number {
        width: 26px;
        height: 26px;
        background: #27AE60;
        border-radius: 26px;
        font-size: 18px;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      .quest-name {
        font-family: DK Lemon Yellow Sun;
        font-style: normal;
        font-weight: normal;
        font-size: 22px;
        line-height: 150%;
        display: flex;
        align-items: center;
        color: #FFFFFF;
        flex: 1;
        margin-left: 15px;
      }
      .reward {
        margin-right: 15px;
        font-style: normal;
        font-weight: bold;
        font-size: 18px;
        line-height: 150%;
        display: flex;
        align-items: center;
        color: #FFFFFF;
        .highlighted {
          color: #FFDE00;
          margin-left: 5px;
        }
      }
    }
    &.disabled {
      .accordion-header {
        opacity: .7;
      }
      &:hover {
        background: transparent;
        transform: translateZ(0);
        z-index: 9999999;
        .bag-tooltip {
          transform: translateX(-50%) scale(1) !important;
          top: 40px;
          width: 270px;
          .tooltip-body {
            padding: 15px 25px;
            white-space: normal;
            font-size: 16px;
            p {
              margin-bottom: 0;
            }
          }
          .tooltip-footer {
            button {
              min-width: 180px;
              height: 35px;
              font-size: 16px;
            }
          }
        }
      }
    }
  }
  &.show {
    overflow: auto;
  }
}
//#endregion Accordion

//#region NG-Boostrap Modal
.modal-backdrop {
  z-index: 99999999 !important;
}

.modal-dialog {
  max-width: 630px;
}

.modal {
  z-index: 999999999 !important;
  .modal-content {
    background-color: $pink-color;
    min-width: 630px;
    .modal-header {
      background-color: #C83787;
      border-bottom: 0;
      padding: 30px 20px;
      .close-modal-button {
        position: absolute;
        right: -17px;
        top: -15px;
        background-image: url('./assets/images/icons/icon-close.svg');
        background-repeat: no-repeat;
        background-size: contain;
        opacity: 1;
        padding: 0;
        margin: 0;
        height: 36px;
        width: 36px;
        outline: none;
      }
      h1 {
        color: $white-color;
        width: 100%;
        text-align: center;
      }
      .modal-title {
        line-height: 1;
      }
    }
    .modal-body {
      padding: 25px;
      &.withdraw {
        .slider-wrapper {
          .slider-background {
            height: 20px;
          }
        }
        .switch-toggle label {
          min-width: 104px;
          &:last-child {
            min-width: 107px;
          }
        }
      }
    }
    .modal-footer {
      padding: 20px 25px;
      button {
        &:nth-child(2) {
          margin-left: 20px;
        }
      }
    }
  }
}
//#endregion NG-Boostrap Modal

//#region Connect-wallet-buttons

.connect-wallet-bridges {
  .no-appearance {
    margin-bottom: 20px;
    background-color: rgba(63, 35, 85, 0.5);
    box-shadow: 0 3px 0 0 rgb(25 17 66 / 86%);
    transition: all 400ms cubic-bezier(0.47, 1.64, 0.41, 0.8);
    border: 1px solid rgb(137 52 116);
    cursor: pointer;
    height: 50px;
    span {
      margin-top: 2px;
    }
    &:after {
      content: '';
      position: absolute;
      display: block;
      border-radius: 50%;
      background-color: red;
      background-image: linear-gradient(
          145deg
        , #024304 0%, #03fe79 100%);
      background-image: -ms-linear-gradient(145deg, #024304 0%, #03fe79 100%);
      width: 29px;
      height: 29px;
      top: auto;
      bottom: -10px;
      right: -10px;
      left: auto;
      box-shadow: 2px 4px 1px 0 rgb(25 17 66 / 86%);
      transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
      transform: scale(0);
    }
    &:before {
      content: "";
      position: absolute;
      display: block;
      background-image: url("assets/images/icons/icon-tick.png");
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
      width: 17px;
      height: 17px;
      top: auto;
      bottom: -4px;
      right: -4px;
      left: auto;
      z-index: 999;
      transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
      transform: scale(0);
    }
    &.active {
      position: relative;
      border: 1px solid rgba(63, 35, 85, 1);
      background-color: rgba(63, 35, 85, 1);
      &:after {
        transform: scale(1)
      }
      &:before {
        transform: scale(1)
      }
    }
    &:hover {
      border: 1px solid rgba(63, 35, 85, 1);
      transform: scale(1.025);
      filter: brightness(1.1);
      background-color: #3f2355;
    }
    &:last-child {
      margin-bottom: 0;
    }
    img {
      width: 37px;
    }
  }
  .link {
    font-size: 18px;
  }
}

//#endregion Connect-wallet-buttons

//#region bag-wizard
.bag-wizard {
  width: 70%;
  margin: auto;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  position: relative;
  top: -9px;
  .bag-wizard-step {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 28px;
    height: 28px;
    border: 1px solid #FFFFFF;
    opacity: .5;
    position: relative;
    border-radius: 28px;
    transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
    cursor: not-allowed;
    transform: translateZ(0);
    &.current {
      background: $pink-color;
      box-shadow: 1px 2px 2px rgba(112, 36, 100, 0.9);
      opacity: 1;
      cursor: default;
    }
    &.passed {
      opacity: 1;
      cursor: pointer;
      &:after {
        content: '';
        position: absolute;
        display: block;
        border-radius: 50%;
        background-image: linear-gradient(
            145deg
          , #024304 0%, #03fe79 100%);
        background-image: -ms-linear-gradient(145deg, #024304 0%, #03fe79 100%);
        width: 29px;
        height: 29px;
        top: auto;
        bottom: 0;
        right: -1px;
        left: auto;
        box-shadow: 1px 2px 2px rgba(112, 36, 100, 0.9);
        transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
        transform: scale(1);
      }
      &:before {
        content: "";
        position: absolute;
        display: block;
        background-image: url("assets/images/icons/icon-tick.png");
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        width: 16px;
        height: 16px;
        top: auto;
        bottom: 5px;
        right: 6px;
        left: auto;
        z-index: 999;
        transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
        transform: scale(1);
      }
    }
    .step-name {
      white-space: nowrap;
    }
  }
  .bag-steps-connector {
    height: 1px;
    width: 100%;
    background: white;
  }
}
//#endregion bag-wizard

//#region Toggle switch
.switch-toggle {
  background: $dark-blue;
  border-radius: 5px;
  padding: 3px;
  flex: 1;
  margin-right: 20px;
}
.switch-toggle input {
  position: absolute;
  opacity: 0;
}

.switch-toggle label {
  margin-bottom: 0;
}
.switch-toggle input + label {
  padding: 7px 15px;
  float:left;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: Myriad Pro;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  color: #FFFFFF;
}
.switch-toggle input:checked + label {
  background: $pink-color;
  box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.35);
  border-radius: 5px;
  color: white;
}
//#endregion Toggle switch

//#region bag-select-option
.select-options {
  display: flex;
  flex-direction: column;
  .select-options-set {
    display: flex;
    width: 100%;
  }
  .section-buttons-set {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-top: 20px;
    .custom-control {
      position: relative;
      top: 9px;
    }
  }
}
.bag-select {
  background: $dark-blue;
  margin-right: 15px;
  border-radius: 5px;
  width: 100%;
  &:last-child {
    margin-right: 0;
  }
  .bag-select-wrapper {
    position: relative;
    .bag-select-trigger {
      padding: 10px 15px;
      display: flex;
      justify-content: space-between;
      background: #393467;
      border-radius: 5px 5px 0 0;
      cursor: pointer;
      &:hover {
        .options {
          transform: scale(1);
        }
      }
      .options {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 40px;
        left: 0;
        right: 0;
        transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
        transform: scale(0);
        background-color: $dark-blue;
        border-radius: 0 0 5px 5px;
        overflow: hidden;
        z-index: 9999;
        .option {
          padding: 10px 15px;
          cursor: pointer;
          background-color: $dark-blue;
          transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
          img {
            height: 20px;
            margin-right: 10px;
          }
          .result-addon {
            font-size: 14px;
            background: rgba(255,255,255, .5);
            padding: 6px 10px;
            border-radius: 5px;
            color: black;
            margin-right: 10px;
          }
          &:hover {
            filter: brightness(1.15);
          }
        }
      }
    }
    .bag-preview-result {
      padding: 10px 15px;
      display: flex;
      align-items: center;
      .result-addon {
        font-size: 14px;
        background: rgba(255,255,255, .5);
        padding: 6px 10px;
        border-radius: 5px;
        color: black;
        margin-right: 10px;
      }
      img {
        height: 20px;
        margin-right: 10px;
      }
      form {
        width: 100%;
        .form-group {
          display: flex;
          margin-bottom: 0;
          input {
            width: 100%;
            height: auto;
            &::placeholder {
              font-size: 16px;
            }
          }
          button {
            margin-left: 10px;
            height: 35px;
            padding: 10px 13px;
            border-radius: 5px;
          }
        }
      }
    }
  }
}
//#endregion bag-select-option

//#region FAQ page
.faq-page-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  .switch-toggle {
    margin-bottom: 25px;
    flex: 0;
  }
}
.questions-wrapper {
  display: flex;
  align-items: flex-start;
  > div {
    width: 50%;
  }
  .left-column {
    margin-right: 20px;
  }
  .right-column {
    margin-left: 20px;
  }
}
//#endregion FAQ page

//#region Bag-accordions
.bag-accordion-wrapper {
  border-bottom: 1px solid rgb(255 255 255 / 30%);
  transition: all 300ms linear;
  margin: 10px 0;
  overflow: hidden;
  .bag-accordion-header {
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
    transition: all 300ms linear;
    h1 {
      font-size: 26px;
      margin: 0;
      color: $white-color;
    }
    img {
      transition: all 100ms linear;
      transform: rotate(0);
      height: 33px;
    }
    &:hover {
      h1 {
        color: $body-text-yellow;
      }
    }
  }
  .bag-accordion-body {
    background-color: $dark-blue;
    border-radius: 5px;
    transform: scale(0);
    height: 0;
    transition: all 300ms linear;
    p {
      margin-bottom: 0;
      font-size: 18px;
    }
  }
  &.open {
    padding-bottom: 25px;
    .bag-accordion-header {
      img {
        transform: rotate(45deg);
      }
      h1 {
        color: $body-text-yellow;
      }
    }
    .bag-accordion-body {
      transform: scale(1);
      height: auto;
      padding: 15px;
    }
  }
}
//#endregion Bag-accordions

//#region Loot-bag
.loot-bag-wrapper {
  width: 50px;
  height: 30px;
  display: flex;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.loot-bag {
  width: 100%;
  flex: 1;
  align-items: flex-end;
  justify-content: center;
  position: relative;
  z-index: 9999;
  margin: 10px 20px 0 20px;
  .box {
    position: absolute;
    top: 4px;
    bottom: 0;
    left: 0;
    transform: translate(0);
    font-size: 32px;
    -webkit-animation: pop 1.2s infinite;
    animation: pop 1.2s infinite;
    animation-duration: 1.5s;
    height: 30px;
    width: 30px;
    cursor: pointer;
    img {
      height: 30px;
      width: auto;
    }
  }
}
.light {
  -webkit-animation: aura 1.2s infinite;
  animation: aura 1.2s infinite;
  animation-duration: 1.5s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(50%);
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #fff;
  box-shadow:
          0 0 15px 7px #fff,
          0 0 25px 15px #f0f,
          0 0 35px 22px #0ff;
}
//#endregion Loot-bag

//#region Shop page
.shop-page-wrapper {
  .bagTabs {
    .bag-tabs-content {
      min-height: 220px;
      height: auto;
    }
    label {
      max-width: 200px;
    }
    .form-group {
      margin-bottom: 0;
    }
  }
  .store-items-wrapper {
    display: flex;
    padding: 25px;
    .store-items {
      width: 55%;
      margin-right: 25px;
      .store-items-filters {
        display: flex;
        > h3 {
          white-space: nowrap;
          color: white;
          margin-right: 40px;
        }
        form {
          width: 100%;
          display: flex;
          .form-group {
            flex: 1;
          }
          .custom-select {
            select {
              width: calc(100% - 40px);
            }
            &:after {
              right: 51px;
            }
          }
          .sorting-arrow {
            right: 6px;
            top: 50%;
            transform: translateY(-50%);
            position: absolute;
            background-color: #5c3b74;
          }
        }
      }
      .items {
        padding: 0 0 25px 0;
        display: flex;
        flex-wrap: wrap;
        align-content: flex-start;
        height: calc(100vh - 335px);
        overflow: auto;
        margin-top: 25px;
        .item{
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: space-between;
          position: relative;
          width: calc((100% / 3) - 14px);
          min-height: 220px;
          background: transparent;
          border-radius: 10px;
          padding: 15px 15px 25px 15px;
          margin: 0 20px 25px 0;
          border: 2px solid;
          cursor: pointer;
          transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
          &:after {
            content: '';
            background-position: center;
            background-repeat: no-repeat;
            background-size: contain;
            width: 40px;
            height: 40px;
            display: block;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            bottom: -20px;
          }
          &.rare {
            border-color: #ff7f00;
            &:after {
              background-image: url("~/assets/images/avatars/rarity-stars/rare-star-badge.svg");
            }
          }
          &.epic {
            border-color: #FFE600;
            &:after {
              background-image: url("~/assets/images/avatars/rarity-stars/epic-star-badge.svg");
            }
          }
          &.legendary {
            border-color: #D4D3D7;
            &:after {
              background-image: url("~/assets/images/avatars/rarity-stars/legendary-star-badge.svg");
            }
          }
          &.mythic {
            border-color: #00E413;
            &:after {
              background-image: url("~/assets/images/avatars/rarity-stars/mythic-star-badge.svg");
            }
          }
          &.exotic {
            border-color: #00FFFF;
            &:after {
              content:'';
              background-image: url("~/assets/images/avatars/rarity-stars/exotic-star-badge.svg");
            }
          }
          &:hover {
            background-color: #2E2959;
          }
          &.active {
            background-color: #2E2959;
          }
          &:nth-child(3n) {
            margin-right: 0;
          }
          .avatar-names {
            font-family: DK Lemon Yellow Sun;
            font-style: normal;
            font-weight: normal;
            font-size: 20px;
            line-height: 150%;
            letter-spacing: 2px;
            color: #FFDE00;
            text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
          }
          .avatar-image {
            max-width: 100%;
            height: 100px;
          }
          button {
            display: block;
            width: 100%;
            transform: none;
            background: #171431;
            box-shadow: none !important;
            border-radius: 30px;
          }
        }
      }
    }
    .item-preview-panel {
      background: #2E2959;
      border-radius: 10px;
      padding: 25px;
      text-align: center;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 45%;
      .bag-carousel {
        position: relative;
        margin-bottom: 25px;
        .bag-carousel-image-preview {
          img {
            height: 247px;
          }
        }
        .bag-carousel-controllers {
          .slider-controller {
            background-color: transparent;
            border: none;
            outline: none;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            &.prev {
              transform: translateY(-50%) scaleX(-1) !important;
              left: 0;
            }
            &.next {
              right: 0;
            }
          }
        }
      }
      .bag-carousel-description {
        background: rgba(255, 255, 255, 0.11);
        border-radius: 10px;
        padding: 20px;
      }
    }
    @media (max-width: 1280px) {
      flex-direction: column-reverse;
      .store-items {
        width: 100%;
        .items {
          height: auto !important;
        }
      }
      .item-preview-panel {
        width: 100%;
        margin-bottom: 25px;
      }
    }
  }
}
//#endregion Shop page


